import { Controller, Scene } from "react-scrollmagic";
import React, { useState,Suspense, useRef, useFrame } from 'react';
import { Canvas, useThree } from 'react-three-fiber';
import Lights from './Three/lights'
import Floor from './Three/floor';
import {softShadows, Loader, OrbitControls } from '@react-three/drei'
import "./About.css";

softShadows()

const Box = ({ position, args, color}) => {
  const mesh = useRef(null);
  // useFrame(() => (mesh.current.rotation.x = mesh.current.rotation.y += 0.01));
  return (
    <mesh castShadow position={position} ref={mesh}>

      <boxBufferGeometry attach="geometry" args={args} color={color} />
    </mesh>
  );
};

const About = () => {
  const [open, setOpen] = useState(false)
  return (
    <div className="container">
      <div className="container-inner" id="Skillset">
        <div className="empty-row">
          <div className="line" />
        </div>
        <div className="content-row" id="trigger">
          <Controller>
            <div className="Left-column">
                <div className="EmptySpace">
              <Scene classToggle="typewriter" triggerElement="#trigger">
                <div>
                  <h1 className="Skills">Skills and Experiences</h1>
                </div>
              </Scene>
              </div>
              <div className="Skill-Words">
              <p>
                I am mostly experienced in using Python, HTML, CSS, Javascript
                and Reactjs
              </p>
              <p>
                However, I have many exposures to different kind of coding languages
                and still learning
              </p>
              <p>
              </p>
              </div>
            </div>
            <div className="right-column">
            {/* <Canvas
        colorManagement
        shadows
        
        >
          <Lights />
          <Suspense fallback={null}>
            <Box position={[0,1,0]} args={[1,1,1]} /> 
            <Floor />
            <OrbitControls/>
    
          </Suspense>
    
        </Canvas>
        <Loader /> */}
    

            </div>
          </Controller>
        </div>
      </div>
    </div>
  );
};

export default About;
