import React from 'react'
import Video from '../../videos/Comp_8.mp4'
import styled from 'styled-components'


const Hero = styled.video`
    position: fixed;
    overflow: hidden;
    width:100%;
    height:100%;
    object-fit:cover;
    margin: 0;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -999;
`

const HeroBg = () => {
    return (
        <Hero autoPlay loop muted src={Video} type='video/mp4'/>
    )
}

export default HeroBg
